.border {
	border-radius: 50%;
	background: linear-gradient(to right, rgba(128, 26, 178, 1), rgba(76, 76, 255, 1));
	padding: 2px;
}
.item {
	width: 64px;
	height: 64px;
	border-radius: 50%;
	background-color: #131316;
	box-sizing: border-box;
}
.img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}