.popup {
	display: flex;
	flex-direction: column;
	justify-items: center;
	width: 720px;
	padding-top: 12px;
}

.name {
	text-align: center;
}

.details {
	padding: 0;
	margin-top: 0;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(100px, 120px));
	column-gap: 20px;
	list-style: none;
	justify-content: center;
}

.detail {
	text-align: center;
}